@use "../../child-theme-main/styles/base/variables";
@use "../../child-theme-main/styles/base/mixins";

@font-face {
  font-family: "Dillan";
  src: url("../fonts/made-dillan.woff2") format("woff2"),
    url("../fonts/made-dillan.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

.poster-maker {
  width: 100vw !important; // GPI Override.
  background-color: variables.$anthracite;
  left: 50%;
  transform: translateX(-50%);
  padding: 4em 0;
  color: white;

  .row {
    padding: 0 2rem;
  }
}

.canvas_input_label {
  font-size: 24px;
  margin: 2rem 0 0;
  font-weight: 500;
}

#canvas_input {
  display: block;
  margin: 1rem 0 0;
}

#canvas_download {
  display: block;
  background-color: #f36d3a;
  padding: 1em 2em;
  font-weight: 500;
  color: white;
  border: none;
  margin: 2rem 0 0;

  &:hover {
    cursor: pointer;
    background-color: #f3593c;
  }
}

.canvas_container {
  position: relative;
  width: 100%;
  height: auto;

  &:before {
    content: "";
    padding-top: 141.428571429%;
    display: block;
  }
}

.canvas_wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#canvas_poster {
  border: 1px solid #000;
  max-height: 100%;
}

.canvas_user_input_container {
  padding: 0 16px 16px 0;
}

.canvas_user_input_container .help-text {
  color: #eaeaea;
}

@media screen and (min-width: 775px) {
  .canvas_container {
    width: 40%;
    height: auto;
  }

  .canvas_user_input_container {
    width: 60%;
    padding: 0 1rem 0 2rem;

    textarea {
      max-width: 300px;
    }
  }

  .canvas_input_label {
    margin: 0;
  }
}
